@font-face {
  font-family: LyonText;
  src: url('./LyonText-Regular.otf') format('opentype');
}

@font-face {
  font-family: Graphik-Regular;
  src: url('./Graphik-Regular.otf') format('opentype');
}

@font-face {
  font-family: Graphik-Medium;
  src: url('./Graphik-Medium.otf') format('opentype');
}

a {
  color: black;
  text-decoration: none;
}

.sticky {
  position: -webkit-sticky !important; /* Safari */
  position: sticky !important;
  align-self: flex-start; /* <-- this is the fix */
  z-index: 998;
}

.large-headline {
  font-family: LyonText;
  font-size: 44px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.3;
  letter-spacing: -0.92px;
}

.medium-headline {
  font-family: LyonText;
  font-size: 32px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 38px;
  letter-spacing: -0.32px;
}

.small-headline {
  font-family: LyonText;
  font-size: 24px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.25;
  letter-spacing: -0.2px;
}

.medium-body {
  font-family: LyonText;
  font-size: 18px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.44;
  letter-spacing: -0.1px;
}

.small-body {
  font-family: Graphik-Regular;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.29;
  letter-spacing: normal;
}

.medium-caption {
  font-family: Graphik-Medium;
  font-size: 12px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: normal;
}

.truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.grid__row {
  padding-left: 11px;
  padding-right: 11px;
}

.grid__container {
  margin-left: 3rem !important;
  margin-right: 3rem !important;
}

@media screen and (max-width: 1439px) {
  .grid__container {
    margin-left: 58px !important;
    margin-right: 58px !important;
  }
}

@media screen and (max-width: 1055px) {
  .grid__container {
    margin-left: 3rem !important;
    margin-right: 3rem !important;
  }
}

@media screen and (max-width: 755px) {
  .grid__container {
    margin-left: 10px !important;
    margin-right: 10px !important;
  }
}

@media screen and (max-width: 374px) {
  .grid__container {
    margin-left: 10px !important;
    margin-right: 10px !important;
  }
}
